<template>
    <AppBar :title=title :centerTitle="false" back='category' />
    <!-- exam section -->
    <div v-if="parent=='exam'">
      <div class="flex flex-row fixed right-4 h-14 text-white items-center justify-center">
        <div class="flex-1 text-lg text-right">{{getTime}}</div>
      </div>
      <div class="pt-14"></div>
      <div class="mx-auto md:container md:px-24">
      <MultipleChoice1 :year="year" :type="type" mondai="mondai1" />
      <MultipleChoice1 :year="year" :type="type" mondai="mondai2" />
      <MultipleChoice1 :year="year" :type="type" mondai="mondai3" />
      <MultipleChoice2 :year="year" :type="type" mondai="mondai4" />
      <MultipleChoice2 :year="year" :type="type" mondai="mondai5" />
      <Finish />
      </div>
      <div class="my-20"></div>
    </div>
    <!-- exam section -->

    <!-- result section -->
    <div v-if="parent=='showresult'">
      <div class="pt-14"></div>
      <div class="mx-auto md:container md:px-24">
        <ResultMultipleChoice1 :year="year" :type="type" mondai="mondai1" />
        <ResultMultipleChoice1 :year="year" :type="type" mondai="mondai2" />
        <ResultMultipleChoice1 :year="year" :type="type" mondai="mondai3" />
        <ResultMultipleChoice2 :year="year" :type="type" mondai="mondai4" />
        <ResultMultipleChoice2 :year="year" :type="type" mondai="mondai5" />
      </div>
      <div class="my-20"></div>
    </div>
    <!-- result section -->

    <!-- score section -->
    <div class="mx-auto md:container md:px-24">
      <Result :parent="parent" :year="year" :type="type" />
    </div>
    <!-- score section -->
</template>

<script>
// @ is an alias to /src
import AppBar from '@/components/AppBar.vue';
// exam
import MultipleChoice1 from '@/components/mojigoi/MultipleChoice1.vue';
import MultipleChoice2 from '@/components/mojigoi/MultipleChoice2.vue';
import Finish from '@/components/Finish.vue';
// result
import ResultMultipleChoice1 from '@/components/mojigoi/result/ResultMultipleChoice1.vue';
import ResultMultipleChoice2 from '@/components/mojigoi/result/ResultMultipleChoice2.vue';
// Total Score
import Result from '@/components/Result.vue';

export default {
  name: 'MojiGoiView',
  components: {
    AppBar,
    MultipleChoice1,
    MultipleChoice2,
    Finish,
    ResultMultipleChoice1,
    ResultMultipleChoice2,
    Result
  },
  data: function() { 
    return {
      parentData: 'exam',
      timeleft: 0,//1500,
    };
  },
  mounted() {
    this.$store.dispatch('storeInit');
    this.start();
  },
  methods: {
    callBack: function() {
      let moji = this.$store.state[this.year].moji; 
      
      // mondai1
      for (let a=0;a<moji.mondai1.questions.length;a++){
        let question = moji.mondai1.questions[a];
        if(question.pick){
          this.$store.state[this.year].moji.mondai1.questions[a].pick='';
        }
      }
      
      // mondai2
      for (let a=0;a<moji.mondai2.questions.length;a++){
        let question = moji.mondai2.questions[a];
        if(question.pick){
          this.$store.state[this.year].moji.mondai2.questions[a].pick='';
        }
      }
      
      // mondai3
      for (let a=0;a<moji.mondai3.questions.length;a++){
        let question = moji.mondai3.questions[a];
        if(question.pick){
          this.$store.state[this.year].moji.mondai3.questions[a].pick='';
        }
      }
      
      // mondai4
      for (let a=0;a<moji.mondai4.questions.length;a++){
        let question = moji.mondai4.questions[a];
        if(question.pick){
          this.$store.state[this.year].moji.mondai4.questions[a].pick='';
        }
      }
      
      // mondai5
      for (let a=0;a<moji.mondai5.questions.length;a++){
        let question = moji.mondai5.questions[a];
        if(question.pick){
          this.$store.state[this.year].moji.mondai5.questions[a].pick='';
        }
      }
    },
    setScore: function() {
      this.$store.dispatch('storeInit');
      let score = this.$store.state.scores;
      let moji = this.$store.state[this.year].moji;
      let mark=0;
      let count=0;     
      
      // mondai1
      for (let a=0;a<moji.mondai1.questions.length;a++){
        count+=1;
        let question = moji.mondai1.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark+=1;
          }
          this.$store.state[this.year].moji.mondai1.questions[a].pick='';
        }
      }
      
      // mondai2
      for (let a=0;a<moji.mondai2.questions.length;a++){
        count+=1;
        let question = moji.mondai2.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark++;
          }
          this.$store.state[this.year].moji.mondai2.questions[a].pick='';
        }
      }
      
      // mondai3
      for (let a=0;a<moji.mondai3.questions.length;a++){
        count+=2;
        let question = moji.mondai3.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark+=2;
          }
          this.$store.state[this.year].moji.mondai3.questions[a].pick='';
        }
      }
      
      // mondai4
      for (let a=0;a<moji.mondai4.questions.length;a++){
        count+=2;
        let question = moji.mondai4.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark+=2;
          }
          this.$store.state[this.year].moji.mondai4.questions[a].pick='';
        }
      }
      
      // mondai5
      for (let a=0;a<moji.mondai5.questions.length;a++){
        count+=2;
        let question = moji.mondai5.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark+=2;
          }
          this.$store.state[this.year].moji.mondai5.questions[a].pick='';
        }
      }

      score[this.year].vocab_knowledge = Math.round( (mark*60)/count );
      
      this.$store.dispatch('setScore',score);
      this.$store.dispatch('setTimeLeft',-2);
      this.parentData='score';
    },
    getTimeLeft: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.timeLeft;
    },
    setTimeLeft: function(time) {
      this.$store.dispatch('setTimeLeft',time);
    },
    start: async function() {
      this.timeleft = this.getTimeLeft();
      if(this.timeleft==-1){
        this.parentData='showresult';
      }else if(this.timeleft==-2){
        this.parentData='score';
      }else{
        await this.sleep(1000);
        if(this.timeleft>0&&this.parentData=='exam'&&this.$route.name=='mojigoi'){
          this.timeleft--;
          this.setTimeLeft(this.timeleft);
          this.start();
        }else{
          this.setTimeLeft(-1);
          this.parentData='showresult';
        }
      }
      
    },
    sleep: function(ms) {
      return new Promise((resolve) => {
        setTimeout(resolve, ms);
      });
    }
  },
  computed: {
    title: function() {
      this.$store.dispatch('storeInit');
      return this.parentData!='score'?this.$store.state.currentTitle + " 文字語彙":'score';
    },
    year: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentYear;
    },
    type: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentType;
    },
    parent: function() {
      return this.parentData;
    },
    getTime: function() {
      let time = this.timeleft;
      let hh = Math.floor( time/60 );
      let mm = time%60;
      let format = hh+ (mm<10? ':0' : ':') +mm;
      return format;
    }
  }
}
// {"j2018":{"vocab_knowledge":60,"grammer_reading":57,"listening":60}}
</script>


