<template>
  <div class="pt-5"></div>
  <!-- mondai title -->
  <div 
    v-html="titleLead($store.state[year][type][mondai].title)" 
    class="font-bold px-3 pt-2  pb-4 lg:pb-8 text-sm select-none"
  ></div>
  <!-- Boxs -->
  <div 
    v-for="(box,index) in $store.state[year][type][mondai].boxs"
    v-bind:key="index"
  >
    <!-- explain -->
    <div class="px-3 py-4 text-sm text-left select-none"> {{box.explain}} </div>
    <!-- box -->
    <div class="mx-5 my-5 py-4 lg:py-8 px-2 lg:px-8 border-2 border-gray-500 select-none">
      <!-- title -->
      <div 
        v-for="(title,index) in box.title.contents"
        v-bind:key="index"
        v-bind:class="style(box.title.style)"
      >
        <div>{{title}}</div>
      </div>
      <!-- subTitle -->
      <div 
        v-for="(subTitle,index) in box.subTitle.contents"
        v-bind:key="index"
        v-bind:class="style(box.subTitle.style)"
      >
        <div>{{subTitle}}</div>
      </div>
      <!-- paragraphs -->
      <div 
        v-for="(paragraph,index) in box.paragraphs"
        v-bind:key="index"
      >
        <div v-html="paragraphs(paragraph)" class="py-1 leading-loose"></div>
      </div>
      <!-- tail -->
      <div 
        v-for="(tail,index) in box.tail.contents"
        v-bind:key="index"
        v-bind:class="style(box.tail.style)"
      >
        <div>{{tail}}</div>
      </div>
    </div>
    <!-- answer -->
    <div class="my-3"></div>
    <div
      v-for="(answerBox,index) in $store.state[year][type][mondai].answerBoxs"
      v-bind:key="index"
    >
      <!-- number -->
      <div class="mx-3 pt-2 pb-4 select-none">({{answerBox.number}})</div>
      <!-- button -->
      <div v-bind:class="grid(answerBox.answers)" class="px-5 pb-4 grid gap-2 md:gap-6 select-none">
        <div 
          v-for="(answer,i) in answerBox.answers"
          v-bind:key="i"
        >
          <div
            v-if="answerBox.pick==answer&&answerBox.true==answer"
            class="flex flex-row w-full h-12 px-3 bg-green-100 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
            <div class="pl-2 text-xs font-bold text-left">{{answer}}</div>
          </div>
          <div
            v-if="answerBox.pick==answer&&answerBox.true!=answer"
            class="flex flex-row w-full h-12 px-3 bg-red-100 items-center justify-start rounded-md">
            <Icon :path="mdiCloseCircleOutline" size="20" class="text-red-500"/>
            <div class="pl-2 text-xs font-bold text-left">{{answer}}</div>
          </div>
          <div
            v-if="answerBox.pick!=answer&&answerBox.true!=answer"
            class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxBlankCircleOutline" size="20" class="text-gray-500"/>
            <div class="pl-2 text-xs text-left">{{answer}}</div>
          </div>
          <div
            v-if="answerBox.pick!=answer&&answerBox.true==answer"
            class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
            <div class="pl-2 text-xs text-left">{{answer}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {  mdiCheckboxMarkedCircleOutline, mdiCloseCircleOutline ,mdiCheckboxBlankCircleOutline } from '@mdi/js';
import Icon from '@/components/Svg.vue';
export default {
  data: ()=> ({  mdiCheckboxMarkedCircleOutline, mdiCloseCircleOutline ,mdiCheckboxBlankCircleOutline }),
  components: {
    Icon
  },
  name: 'ResultMultipleParagraph',
  props: {
    year: String,
    type: String,
    mondai: String,
  },
  methods: {
    grid: function(ans) {
      var overFlow = false;
      ans.forEach(e => {
        if(e.length>10){
          overFlow=true;
        }
      });
      return overFlow?'grid-cols-1':'grid-cols-2';
    },
    style: function(styles) {
      var style = '';
      for (const [key,value] of Object.entries(styles)) {
        style+=value + ' ';
      }
      return style;
    },
    titleLead: function(title) {
        var myArray = title.split('｜');
        var html='';
        html+=`<div class="flex flex-row text-md flex-wrap">`;
        myArray.forEach(ans => {
            if(ans.includes('！')){
                let star = ans.substring(1);             
                html+=`<div class="underline">&nbsp&nbsp&nbsp${star}&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                let tmpArray = ans.split('');
                tmpArray.forEach(ele => {
                   html+=`<div>${ele}</div>`; 
                });
            }
        });
        html+=`</div>`;
        return  html;
    },
    paragraphs: function(text) {
        var myArray = text.split('｜');
        var html='';
        html+=`
          <div class="flex flex-row text-x11 flex-wrap">
          <div >&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</div>
        `;
        myArray.forEach(ans => {
            if(ans.includes('！')){
                let star = ans.substring(1);             
                html+=`<div class="underline font-bold">&nbsp&nbsp&nbsp${star}&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                let tmpArray = ans.split('');
                tmpArray.forEach(ele => {
                   html+=`<div>${ele}</div>`; 
                });
            }
        });
        html+=`</div>`;
        return  html;
    },
  }
}
</script>

