<template>
    <!-- Main modal -->
  <div v-show="$parent.model" class="overflow-y-auto overflow-x-hidden fixed top-0 right-0 left-0 z-50 w-full md:inset-0 h-full md:h-full">
      <div @click="$parent.show_model=false" class="absolute bg-gray-500 opacity-60 w-full h-full">

      </div>
      <div class="relative mx-auto p-4 w-full max-w-2xl h-full md:h-auto">
          <!-- Modal content -->
          <div class="relative bg-white rounded-lg shadow dark:bg-gray-700">
              <!-- Modal header -->
              <div class="flex justify-between items-start p-5 rounded-t border-b dark:border-gray-600">
                  <h3 class="select-none text-xl font-semibold text-gray-900 lg:text-2xl dark:text-white">
                      ဝယ်ယူသုံးစွဲမည်
                  </h3>
                  <button @click="$parent.show_model=false" type="button" class="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white" data-modal-toggle="defaultModal">
                      <svg class="w-5 h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg>  
                  </button>
              </div>
              <!-- Modal body -->
              <div class="p-6 space-y-6">
                  <p class="select-none text-sm text-justify leading-relaxed text-gray-500 dark:text-gray-400">
                      အောက်တွင်ဖော်ပြထားသော code ကို ကူးယူ၍ <span class="text-sm"><a href="https://www.facebook.com/integratedSolution.mm" target="blank" class="font-bold text-blue-500 underline italic">Integrated Solution</a> FaceBook Page</span> ကို message ပေးပို့ပြီးဝယ်ယူနိုင်ပါတယ်။
                  </p>
                    <div class="rounded-lg text-center text-blue-500 font-bold bg-pink-200 border-2 flex flex-row items-center justify-center">
                        {{$store.state.req_keys[$store.state.sector]}}
                        <Copy :text="$store.state.req_keys[$store.state.sector]" id_name="#rq_code" class="text-blue-800 ml-3 my-2 border-blue-500"/>
                    </div>
                    <p class="select-none text-sm text-justify leading-relaxed text-gray-500 dark:text-gray-400">
                        ဝယ်ယူထားသော unlock code ကိုအောက်ပါ Input Field တွင်ထည့်၍ Unlock ကိုနိပ်ပြီး ဆက်လက်ဆောင်ရွက်နိုင်ပါသည်။
                    </p>
                  <input v-model="input" class="w-full rounded-full text-center border-gray-500 border-2 px-6 text-lg" type="text">

                  <div class="text-sm text-red-500" v-if="error">{{error_}}</div>
                  <div class="text-sm text-blue-500" v-if="success">{{success_}}</div>
              </div>
              <!-- Modal footer -->
              <div class="flex justify-center p-6 space-x-2 rounded-b border-t border-gray-200 dark:border-gray-600">
                  <button @click="unlock" type="button" class="select-none text-white bg-gradient-to-r from-indigo-500 to-blue-500 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Unlock</button>
              </div>
              <input type="hidden" id="rq_code"/>
          </div>
      </div>
  </div>
</template>

<script>
import Copy from '@/components/Copy.vue';
export default {
  name: 'LicenseModel',
  components: {
      Copy
  },
  data: function() {
      return{
          input: '',
          error: '',
          success: '',
      }
  },
  methods: {
    unlock: function() {
        this.error='';
        this.success='';

        let unlock = this.$store.state.uuid + '-' + this.input;
        let source = this.$store.state.uuid + '-' + this.$store.state.unlock_keys[this.$store.state.sector];
        if(unlock==source){
            this.success='Unlock အောင်မြင်သည်။ ကျေးဇူးအထူးတင်ရှိပါသည်။';
            localStorage.setItem('license', unlock);
            this.$router.go(0);
        }else{
            this.error='unlock ကုတ်မှားယွင်းနေပါသည်။';
        }
    }
  },
  computed:{
    success_: function(){
        return this.success;
    },
    error_: function(){
        return this.error;
    },
  }
}
</script>