<template>
    <AppBar title='score' :centerTitle="true" back='category' />
    <!-- score section -->
      <Result :parent="parent" :year="year" :type="type" />
    <!-- score section -->
</template>

      

<script>
// @ is an alias to /src
import AppBar from '@/components/AppBar.vue';
// Total Score
import Result from '@/components/Result.vue';
export default {
  name: 'ScoreView',
  components: {
    AppBar,
    Result
  },
  data: function() {
    return {
      parentData: 'score',
    };
  },
  methods: {
    callBack: function() {      
    },
  },
  computed: {
    year: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentYear;
    },
    type: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentType;
    },
    parent: function() {
      return this.parentData;
    },
  }
}
</script>