      <!-- fail -->
      <div v-if="fail" class="h-14 flex flex-row items-center justify-center bg-gradient-to-r from-pink-500 to-pink-100">
        <Icon :path="mdiEmoticonCry" size="20" class="text-xl mx-6 text-blue-900"/>
