    </div>

    <!-- <button 
      @click="setScore()"
      class="mx-4 my-4 py-2 bg-gradient-to-r from-indigo-900 to-indigo-600 font-bold text-white text-xl text-center rounded-xl">
          set score
    </button> -->
  </div>
  <!-- score -->
</template>

<script>
import { mdiTrophyOutline, mdiEmoticonCry, mdiHeartBroken } from '@mdi/js';
import Icon from '@/components/Svg.vue';
export default {
  name: 'Result',
  data: ()=> ({mdiTrophyOutline, mdiEmoticonCry, mdiHeartBroken}),
  components: {
    Icon
  },
  props: {
    parent: String,
    year: String,
    type: String,
  },
  methods: {
    setScore: function() {
      this.$store.dispatch('storeInit');
      let score = this.$store.state.scores;
      score[this.year].vocab_knowledge = 18;
      score[this.year].grammer_reading = 30;
      score[this.year].listening = 41;
      this.$store.dispatch('setScore',score);
    },
  },
  computed: {
    pass: function() {
      return this.vocab_knowledge>18&&this.grammer_reading>18&&this.listening>18&&this.total>89;
    },
    fail: function() {
      return this.total!=-1&&this.total<90;
    },
    vocab_knowledge: function() {
      let vocab_knowledge = this.$store.state.scores[this.year].vocab_knowledge;
      return vocab_knowledge;
    },
    grammer_reading: function() {
      let grammer_reading = this.$store.state.scores[this.year].grammer_reading;
      return grammer_reading;
    },
    listening: function() {
      let listening = this.$store.state.scores[this.year].listening;
      return listening;
    },
    total: function() {
      let vocab_knowledge = this.$store.state.scores[this.year].vocab_knowledge;
      let grammer_reading = this.$store.state.scores[this.year].grammer_reading;
      let listening = this.$store.state.scores[this.year].listening;
      let total;
      if(vocab_knowledge>-1&&grammer_reading>-1&&listening>-1){
        total=vocab_knowledge+grammer_reading+listening;
      }else{
        total=-1;
      }
      return total;
    }
  }
}
</script>

