<template>
  <div class="w-screen h-screen flex flex-col items-center justify-center bg-gray-200">
    <img class="w-12 h-12" src="../assets/loading-buffering.gif" alt="loading . . .">
  </div>
</template>

<script>

export default {
  name: 'Loading',
  props: {
    name: String,
  },
  components: {
  },
  methods: {
    toRoute: async function() {
      var name = this.$route.params.name;
      var params = this.$route.params.params;
      await this.sleep(500);
      this.$router.push({ name: name, params: params });
    },
    sleep: function(ms) {
        return new Promise((resolve) => {
            setTimeout(resolve, ms);
        });
    }
  },
  computed: {
    
  },
  created: function() {
      this.toRoute();
  }
}
</script>