<template>
    <button 
      @click="toRoute()"
      class="w-3/4 md:w-2/3 mx-10 my-4 py-2 bg-gradient-to-r from-indigo-500 to-blue-500 font-bold text-white text-xl text-center rounded-xl"
      v-html="hello"
    >
    </button>
</template>

<script>
import Button from '@/components/containers/Button.vue';
export default {
  name: 'ButtonKanji',
  props: {
    name: String,
    text: String,
    params: Object
  },
  methods:{
    toRoute: function(){
      this.$store.dispatch('setCurrentYear',this.params.year);
      this.$store.dispatch('setCurrentTitle',this.params.title);
      this.$store.dispatch('setCurrentType',this.params.type);
      if(this.params.type=='moji'){
        this.$store.dispatch('setTimeLeft',1500);
      }else if(this.params.type=='bunpou'){
        this.$store.dispatch('setTimeLeft',3300); // 3300
      }else{
        this.$store.dispatch('setTimeLeft',-1);
      }
      
      this.$router.push({ name: 'loading', params: {name: this.name,params: this.params} });
    }
  },
  computed: {
    hello: function () {
      let temp = this.text;
      const kanji = temp.split("｜");
      var html = '';
      html+='<div class="flex flex-row flex-wrap justify-center">';
      kanji.forEach(element => {
        if(element.includes("＾")){
          html+=`
            <div class="flex flex-col justify-end">
              <div class="text-xs pr-1px">${element.substring(2)}</div>
              <div class="text-2xl">${element[0]}</div>
            </div>
          `;
        }else{
          html+=`
            <div class="flex flex-col justify-end">
              <div class="text-2xl">${element}</div>
            </div>
          `;
        }
      });
      html+='</div>';
      return html;
    }
  },
}
</script>