<template>
  <!-- title -->
  <div class="pt-5"></div>
  <div 
    v-html="titleLead($store.state[year][type][mondai].title)" 
    class="font-bold px-3 pt-2 pb-4 lg:pb-8 text-sm select-none"
  ></div>  
  <!-- question loop -->
  <div 
      v-for="(question,index) in $store.state[year][type][mondai].questions"
      v-bind:key="index"
      class="py-2 text-sm pb-8 md:pb-14"
    >
      <!-- title -->
      <div
        v-for="(q,x) in question.question"
        v-bind:key="x"
        class="px-3 pb-4 select-none"
      >
        <div 
        v-html="questionLead(q,x)"
        ></div>
      </div>
      
      <div class="px-5 grid grid-cols-2 gap-2 md:gap-6 select-none">
        <div 
          v-for="(answer,i) in question.answers"
          v-bind:key="i"
        >
          <button
            v-if="question.pick!=answer"
            v-on:click="$store.state[year][type][mondai].questions[index].pick=answer"
            class="flex flex-row w-full h-10 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCrosshairs" size="20" class="text-gray-500"/>
            <div class="pl-2 text-xs text-left">{{answer}}</div>
          </button>
          <button 
              v-if="question.pick==answer"
              v-on:click="$store.state[year][type][mondai].questions[index].pick=answer"
              class="flex flex-row w-full h-10 px-3 font-bold bg-gray-300 items-center justify-start rounded-md">
              <Icon :path="mdiCrosshairsGps" size="20" class="text-blue-500 font-bold"/>
              <div class="pl-2 text-xs　text-left">{{answer}}</div>
          </button>
        </div>
      </div>
    </div>
</template>

<script>
import { mdiCrosshairs, mdiCrosshairsGps } from '@mdi/js';
import Icon from '@/components/Svg.vue';
export default {
  data: ()=> ({ mdiCrosshairs, mdiCrosshairsGps }),
  components: {
    Icon
  },
  name: 'MultipleStar',
  props: {
    year: String,
    type: String,
    mondai: String,
  },
  mounted(){
    // $store.state[year][type][mondai].questions;
    let questions = this.$store.state[this.year][this.type][this.mondai].questions;
    questions.forEach( element => element.answers.sort(() => (Math.random() > 0.5) ? 1 : -1) );
  },
  methods: {
    titleLead: function(title) {
        var myArray = title.split('｜');
        var html='';
        html+=`<div class="flex flex-row text-md flex-wrap">`;
        myArray.forEach(ans => {
            if(ans.includes('！')){
                let star = ans.substring(1);             
                html+=`<div class="underline">&nbsp&nbsp&nbsp${star}&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                let tmpArray = ans.split('');
                tmpArray.forEach(ele => {
                   html+=`<div>${ele}</div>`; 
                });
            }
        });
        html+=`</div>`;
        return  html;
    },
    questionLead: function(question,index){
      var myArray = question.split('｜');
      var html='';
      if(index==0){
        html+=`<div class="flex flex-row flex-wrap text-sm">`;
      }else{
        html+=`<div class="flex flex-row flex-wrap text-sm pl-4">`;
      }
      myArray.forEach(e => {
        if(e.includes('！')){
          let subStr = e.substring(1);
          let mySubArray = subStr.split('');
          mySubArray.forEach(s =>{
            if(s=='＿'){
                html+=`<div class="">&nbsp</div>`;
                html+=`<div class="underline ">&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                html+=`<div class="">&nbsp</div>`;
                html+=`<div class="font-bold underline">&nbsp&nbsp${s}&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }
          });
        }else{
          let mySubArray = e.split('');
          mySubArray.forEach(t =>{
            html+=`<div class="">${t}</div>`;
          });
        }
      });
      html+=`</div>`;
      return html;
    }
  }
}
</script>

