<template>
    <!-- $store.dispatch('setCurrentYear',this.params.year) -->
    <a class="fixed bottom-0 flex flex-row justify-end">
        <!-- {{$store.state.ads}} -->
        <div class="md:w-1/2" v-html="getAds" @click="clickAds()">
        </div>
        <!-- <div @click="$store.dispatch('getAds')" class="text-center">Button</div> -->
    </a>
</template>

<script>
export default {
  name: 'Ads',
  data: function() {
    return {
      index: 0,
    }
  },
  mounted() {
    this.start();
  },
  methods: {
    start: async function() {
      while (1!=2){
        this.$store.dispatch('getAds');
        if(this.$store.state.ads.length>0){
          if(this.index<this.$store.state.ads.length-1){
            this.index++;
          }else{
            this.index=0;
          }
        }
        await this.sleep(10000);
      }
    },
    sleep: function(ms) {
      return new Promise((resolve) => {
        setTimeout(resolve, ms);
      });
    },
    clickAds: function() {
      let obj = this.$store.state.ads[this.index];
      if(obj.contactUrl){
        window.location.href = obj.contactUrl;
      }
    }
  },
  computed: {
     getAds: function() {
      var html = '';
      if(this.$store.state.ads.length){
        let obj = this.$store.state.ads[this.index];
        if(obj.imgUrl){
          html += `
            <img src="${obj.imgUrl}" title="${obj.title}">
          `;
        }
      }
      return html;
    },
  }
}
</script>