<template>
  <div class="pt-5"></div>
  <!-- mondai title -->
  <div 
    v-html="titleLead($store.state[year][type][mondai].title)" 
    class="font-bold px-3 pt-2 pb-4 lg:pb-8 text-sm text-justify select-none"
  ></div>

  <!-- question loop -->
  <div 
      v-for="(question,index) in $store.state[year][type][mondai].questions"
      v-bind:key="index"
      class="py-2 text-sm pb-8 md:pb-14"
    >
      <!-- question title -->
      <div 
        v-html="questionLead(`(${question.number})　${question.question}`)" 
        class="px-3 pb-4 select-none"
      ></div>
      
      <div class="px-5 grid grid-cols-1 gap-2 md:gap-6 select-none">
        <div 
          v-for="(answer,i) in question.answers"
          v-bind:key="i"
        >
          <div
            v-if="question.pick==answer&&question.true==answer"
            class="flex flex-row w-full h-12 px-3 bg-green-100 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
            <div v-html="answerLead(answer)" class="pl-2 text-xs font-bold text-left"></div>
          </div>
          <div
            v-if="question.pick==answer&&question.true!=answer"
            class="flex flex-row w-full h-12 px-3 bg-red-100 items-center justify-start rounded-md">
            <Icon :path="mdiCloseCircleOutline" size="20" class="text-red-500"/>
            <div v-html="answerLead(answer)" class="pl-2 text-xs font-bold text-left"></div>
          </div>
          <div
            v-if="question.pick!=answer&&question.true!=answer"
            class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxBlankCircleOutline" size="20" class="text-gray-500"/>
            <div v-html="answerLead(answer)" class="pl-2 text-xs text-left"></div>
          </div>
          <div
            v-if="question.pick!=answer&&question.true==answer"
            class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
            <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
            <div v-html="answerLead(answer)" class="pl-2 text-xs text-left"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- calculate result -->
    <div v-if="mondai=='mondai5'" class="flex flex-row items-center justify-center my-12">
      <button 
        @click="$parent.setScore()"
        class="px-6 my-4 py-2 bg-gradient-to-r from-indigo-500 to-blue-500 font-bold text-white text-xl text-center rounded-xl">
            See Score
      </button>
    </div>
</template>

<script>
import {  mdiCheckboxMarkedCircleOutline, mdiCloseCircleOutline ,mdiCheckboxBlankCircleOutline } from '@mdi/js';
import Icon from '@/components/Svg.vue';
export default {
  data: ()=> ({  mdiCheckboxMarkedCircleOutline, mdiCloseCircleOutline ,mdiCheckboxBlankCircleOutline }),
  components: {
    Icon
  },
  name: 'ResultMultipleChoice2',
  props: {
    year: String,
    type: String,
    mondai: String,
  },
  methods: {
    questionLead: function(title){
      var myArray = title.split('｜');
      var html='';
      html+=`<div class="flex flex-row text-sm">`;
      myArray.forEach(e => {
      if(e.includes('！')){
          let subStr = e.substring(1);
          html+=`<div class="px-2 flex flex-row flex-wrap">`;
          let mySubArray = subStr.split('');
          mySubArray.forEach(s =>{
              html+=`<div class="text-blue-700 border-b-2 border-blue-800 font-semibold">${s}</div>`;
          });
          html+=`</div>`;
      }else{
          let mySubArray = e.split('');
          mySubArray.forEach(t =>{
          html+=`<div class="inline">${t}</div>`;
          });
      }
      });
      html+=`</div>`;
      return html;
    },
    answerLead: function(title){
      var myArray = title.split('｜');
      var html='';
      html+=`<div class="flex flex-row flex-wrap text-xs">`;
      myArray.forEach(e => {
      if(e.includes('！')){
          let subStr = e.substring(1);
          html+=`<div class="px-2 flex flex-row">`;
          let mySubArray = subStr.split('');
          mySubArray.forEach(s =>{
              html+=`<div class="text-blue-500 border-b-2 border-blue-800 font-semibold">${s}</div>`;
          });
          html+=`</div>`;
      }else{
          let mySubArray = e.split('');
          mySubArray.forEach(t =>{
          html+=`<div class="inline">${t}</div>`;
          });
      }
      });
      html+=`</div>`;
      return html;
    },
    titleLead: function(title) {
        var myArray = title.split('｜');
        var html='';
        html+=`<div class="flex flex-row text-md flex-wrap">`;
        myArray.forEach(ans => {
            if(ans.includes('！')){
                let star = ans.substring(1);             
                html+=`<div class="underline">&nbsp&nbsp&nbsp${star}&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                let tmpArray = ans.split('');
                tmpArray.forEach(ele => {
                   html+=`<div>${ele}</div>`; 
                });
            }
        });
        html+=`</div>`;
        return  html;
    },
  }
}
</script>

