<template>
  <div class="pt-3"></div>
  <!-- mondai title -->
  <div
    class="font-bold px-3 pt-2 pb-4 lg:pb-8 text-sm text-justify select-none"
  > {{$store.state[year][type][mondai].title}}
  </div>
  <!-- loop -->
  <div v-for="(question, index) in $store.state[year][type][mondai].questions" :key="index">
    <!-- title -->
    <div class="px-3 py-4 font-bold text-sm select-none">{{question.number}}・ばん</div>
    
    <!-- answer box -->
    <div class="px-5 pt-3 grid grid-cols-3 gap-2 md:gap-6 select-none">
      <div 
        v-for="(answer,i) in question.answers"
        v-bind:key="i"
      >
        <div
          v-if="question.pick==answer&&question.true==answer"
          class="flex flex-row w-full h-12 px-3 bg-green-100 items-center justify-start rounded-md">
          <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
          <div class="pl-2 text-xs font-bold text-left">{{answer}}</div>
        </div>
        <div
          v-if="question.pick==answer&&question.true!=answer"
          class="flex flex-row w-full h-12 px-3 bg-red-100 items-center justify-start rounded-md">
          <Icon :path="mdiCloseCircleOutline" size="20" class="text-red-500"/>
          <div class="pl-2 text-xs font-bold text-left">{{answer}}</div>
        </div>
        <div
          v-if="question.pick!=answer&&question.true!=answer"
          class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
          <Icon :path="mdiCheckboxBlankCircleOutline" size="20" class="text-gray-500"/>
          <div class="pl-2 text-xs text-left">{{answer}}</div>
        </div>
        <div
          v-if="question.pick!=answer&&question.true==answer"
          class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
          <Icon :path="mdiCheckboxMarkedCircleOutline" size="20" class="text-green-500"/>
          <div class="pl-2 text-xs text-left">{{answer}}</div>
        </div>
      </div>
    </div>
  </div>

  <!-- calculate result -->
  <div class="flex flex-row items-center justify-center my-12 mb-24">
    <button 
      @click="$parent.setScore()"
      class="px-6 my-4 py-2 bg-gradient-to-r from-indigo-500 to-blue-500 font-bold text-white text-xl text-center rounded-xl">
          See Score
    </button>
  </div>
</template>

<script>
import {  mdiCheckboxMarkedCircleOutline, mdiCloseCircleOutline ,mdiCheckboxBlankCircleOutline } from '@mdi/js';
import Icon from '@/components/Svg.vue';
export default {
  data: ()=> ({  mdiCheckboxMarkedCircleOutline, mdiCloseCircleOutline ,mdiCheckboxBlankCircleOutline }),
  components: {
    Icon
  },
  name: 'Result4',
  props: {
    year: String,
    type: String,
    mondai: String,
  },
  methods: {

  }
}
</script>

