<template>
    <div class="flex flex-col justify-center items-center select-none mt-4">
        <button 
        @click="result()"
        class="px-4 my-4 py-2 bg-gradient-to-r from-indigo-500 to-blue-500 font-bold text-white text-xl text-center rounded-xl">
            Finish
        </button>
    </div>
</template>

<script>
export default {
  name: 'Finish',
  props: {
    parent: String,
    child: String,
  },
  methods: {
    result: function() {
        window.scrollTo(0,0);
        this.$parent.setTimeLeft(-1);
        this.$parent.parentData='showresult';
    }
  }
}
</script>