<template>
    <div class="fixed w-full">
        <nav class="flex flex-row pl-5 w-full h-14 bg-blue-500 items-center">
            
            <div 
              @click="$parent.onDrop=!$parent.onDrop" v-if="home" 
              class="flex-none cursor-pointer pr-3 py-2"
            >
              <Icon :path="mdiMenu" size="40" class="text-white text-2xl"/>
            </div>
            <div 
              @click="route()" v-if="!home"
              class="flex-none cursor-pointer pr-4 py-2"
            >
                <Icon :path="mdiArrowLeft" size="30" class="text-white text-2xl"/>
            </div>
            <div
              class="flex flex-1 justify-start pr-5"
              v-bind:class="{'justify-center':centerTitle}"
            >
              <div class="text-white text-xl select-none">{{ title }}</div>
            </div>
        </nav>
        <transition
          enter-active-class="transition transition ease-out duration-500"
          enter-class="translate-y-100 opacity-0 scale-y-0"
          enter-to-class="translate-y-1/2 scale-y-100 opacity-100"
          leave-active-class="transform transition duration-400 ease-custom"
          leave-class="translate-y-0 scale-y-100 opacity-100"
          leave-to-class="-translate-y-1/2 scale-y-0 opacity-0"
        >
        <!-- class="flex flex-col w-full absolute bg-blue-300 opacity-80" -->
          <div 
            v-show="drop"
            
            class="flex flex-col w-full absolute bg-gradient-to-r from-blue-500 to-blue-300 opacity-80"
          >
            <button @click="$parent.onDrop=!$parent.onDrop" class="flex flex-row pl-5 items-center h-14 hover:opacity-80 border-b-2">
              <Icon :path="mdiHome" size="20" class="text-white"/>
              <div class="pl-2 text-lg text-white font-bold">Back Home</div>
            </button>
            <router-link to="/about" class="flex flex-row pl-5 items-center h-14 hover:opacity-80 border-b-2">
              <Icon :path="mdiInformation" size="20" class="text-white"/>
              <div class="pl-2 text-lg text-white font-bold">About App</div>
            </router-link>
            <button @click="fbPage()" class="flex flex-row pl-5 items-center h-14 hover:opacity-80">
              <Icon :path="mdiFacebook" size="20" class="text-white"/>
              <div class="pl-2 text-lg text-white font-bold">Facebook Page</div>
            </button>
          </div>
        </transition>
    </div>
</template>

<script>
import { mdiFacebook, mdiInformation, mdiHome, mdiArrowLeft, mdiMenu } from '@mdi/js';
import Icon from '@/components/Svg.vue';
export default {
  name: 'BarNav',
  data: ()=> ({
    mdiFacebook: mdiFacebook,
    mdiInformation: mdiInformation,
    mdiHome: mdiHome,
    mdiArrowLeft: mdiArrowLeft,
    mdiMenu: mdiMenu
  }),
  components: {
    Icon
  },
  props: {
    title: String,
    centerTitle: Boolean,
    home: Boolean,
    back: String,
  },
  methods: {
    route: function() {
      if(this.$parent.callBack()){
        this.$parent.callBack();
      }
      this.$router.push({name: this.back});
    },
    fbPage: function() {
      window.location.href = "https://www.facebook.com/Integrated-solution-106595108344646";
      // https://www.facebook.com/arkarmannaung
    }
  },
  computed: {
    drop: function() {
      return this.$parent.onDrop;
    }
  }
}

</script>