<template>
    <AppBar :title=title :centerTitle="false" back='category' />
    <!-- exam section -->
    <div v-if="parent=='exam'">
      <div class="flex flex-row fixed right-4 h-14 text-white items-center justify-center">
        <div class="flex-1 text-lg text-right">{{getTime}}</div>
      </div>
          <div class="pt-14"></div>
          <div class="mx-auto md:container md:px-24">
          <BunpouMultiple1 :year="year" :type="type" mondai="mondai1" />
          <MultipleStar :year="year" :type="type" mondai="mondai2" />
          <MultipleParagraph :year="year" :type="type" mondai="mondai3" />
          <MultipleParagraph2 :year="year" :type="type" mondai="mondai4" />
          <MultipleParagraph2 :year="year" :type="type" mondai="mondai5" />
          <MultipleParagraph2 :year="year" :type="type" mondai="mondai6" />
          <Finish />
      </div>
    <div class="my-32"></div>
    </div>
    <!-- exam section -->

    <!-- result section -->
    <div v-if="parent=='showresult'">
      <div class="pt-14"></div>
      <div class="mx-auto md:container md:px-24">
      <ResultBunpouMultiple1 :year="year" :type="type" mondai="mondai1" />
      <ResultMultipleStar :year="year" :type="type" mondai="mondai2" />
      <ResultMultipleParagraph :year="year" :type="type" mondai="mondai3" />
      <ResultMultipleParagraph2 :year="year" :type="type" mondai="mondai4" />
      <ResultMultipleParagraph2 :year="year" :type="type" mondai="mondai5" />
      <ResultMultipleParagraph2 :year="year" :type="type" mondai="mondai6" />
      </div>
      <div class="my-20"></div>
    </div>
    <!-- result section -->
    
    <!-- score section -->
    <div class="mx-auto md:container md:px-24">
      <Result :parent="parent" :year="year" :type="type" />
    </div>
    <!-- score section -->
</template>

      

<script>
// @ is an alias to /src
import AppBar from '@/components/AppBar.vue';
// exam
import BunpouMultiple1 from '@/components/bunpou/BunpouMultiple1.vue';
import MultipleChoice1 from '@/components/mojigoi/MultipleChoice1.vue';
import MultipleChoice2 from '@/components/mojigoi/MultipleChoice2.vue';
import MultipleStar from '@/components/bunpou/MultipleStar.vue';
import MultipleParagraph from '@/components/bunpou/MultipleParagraph.vue';
import MultipleParagraph2 from '@/components/bunpou/MultipleParagraph2.vue';
import Finish from '@/components/Finish.vue';
// result
import ResultBunpouMultiple1 from '@/components/bunpou/result/ResultBunpouMultiple1.vue';
import ResultMultipleStar from '@/components/bunpou/result/ResultMultipleStar.vue';
import ResultMultipleParagraph from '@/components/bunpou/result/ResultMultipleParagraph.vue';
import ResultMultipleParagraph2 from '@/components/bunpou/result/ResultMultipleParagraph2.vue';
// Total Score
import Result from '@/components/Result.vue';
export default {
  name: 'BunPouView',
  components: {
    AppBar,
    BunpouMultiple1,
    MultipleChoice1,
    MultipleChoice2,
    MultipleStar,
    MultipleParagraph,
    MultipleParagraph2,
    Finish,
    ResultBunpouMultiple1,
    ResultMultipleStar,
    ResultMultipleParagraph,
    ResultMultipleParagraph2,
    Result
  },
  data: function() {
    return {
      parentData: 'exam',
      timeleft: 0,//1500,
    };
  },
  mounted() {
    this.start();
  },
  methods: {
    callBack: function() {
      let bunpou = this.$store.state[this.year].bunpou;
 
      // mondai1
      for (let a=0;a<bunpou.mondai1.questions.length;a++){
        let question = bunpou.mondai1.questions[a];
        if(question.pick){
          this.$store.state[this.year].bunpou.mondai1.questions[a].pick='';
        }
      }
      // mondai2
      for (let a=0;a<bunpou.mondai2.questions.length;a++){
        let question = bunpou.mondai2.questions[a];
        if(question.pick){
          this.$store.state[this.year].bunpou.mondai2.questions[a].pick='';
        }
      }
      // mondai3
      for (let a=0;a<bunpou.mondai3.answerBoxs.length;a++){
        let answerBox = bunpou.mondai3.answerBoxs[a];
        if(answerBox.pick){
          this.$store.state[this.year].bunpou.mondai3.answerBoxs[a].pick='';
        }
      }
      // mondai4
      for (let a=0;a<bunpou.mondai4.questions.length;a++){
        let question = bunpou.mondai4.questions[a];
        for (let b=0;b<question.answerBoxs.length;b++){
          let answerBox = question.answerBoxs[b];
          if(answerBox.pick){
            this.$store.state[this.year].bunpou.mondai4.questions[a].answerBoxs[b].pick='';
          }
        }
      }
      // mondai5
      for (let a=0;a<bunpou.mondai5.questions.length;a++){
        let question = bunpou.mondai5.questions[a];
        for (let b=0;b<question.answerBoxs.length;b++){
          let answerBox = question.answerBoxs[b];
          if(answerBox.pick){
            this.$store.state[this.year].bunpou.mondai5.questions[a].answerBoxs[b].pick='';
          }
        }
      }
      // mondai6
      for (let a=0;a<bunpou.mondai6.questions.length;a++){
        let question = bunpou.mondai6.questions[a];
        for (let b=0;b<question.answerBoxs.length;b++){
          let answerBox = question.answerBoxs[b];
          if(answerBox.pick){
            this.$store.state[this.year].bunpou.mondai6.questions[a].answerBoxs[b].pick='';
          }
        }
      }
          
    },
    setScore: function() {
      this.$store.dispatch('storeInit');
      let score = this.$store.state.scores;
      let bunpou = this.$store.state[this.year].bunpou;
      let mark=0;
      let count=0;     
      // mondai1
      for (let a=0;a<bunpou.mondai1.questions.length;a++){
        count+=2;
        let question = bunpou.mondai1.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark+=2;
          }
          this.$store.state[this.year].bunpou.mondai1.questions[a].pick='';
        }
      }
      // mondai2
      for (let a=0;a<bunpou.mondai2.questions.length;a++){
        count+=2;
        let question = bunpou.mondai2.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark+=2;
          }
          this.$store.state[this.year].bunpou.mondai2.questions[a].pick='';
        }
      }
      // mondai3
      for (let a=0;a<bunpou.mondai3.answerBoxs.length;a++){
        count+=2;
        let answerBox = bunpou.mondai3.answerBoxs[a];
        if(answerBox.pick){
          if(answerBox.pick==answerBox.true){
            mark+=2;
          }
          this.$store.state[this.year].bunpou.mondai3.answerBoxs[a].pick='';
        }
      }
      // mondai4
      for (let a=0;a<bunpou.mondai4.questions.length;a++){
        let question = bunpou.mondai4.questions[a];
        for (let b=0;b<question.answerBoxs.length;b++){
          count+=3;
          let answerBox = question.answerBoxs[b];
          if(answerBox.pick){
            if(answerBox.pick==answerBox.true){
              mark+=3;
            }
            this.$store.state[this.year].bunpou.mondai4.questions[a].answerBoxs[b].pick='';
          }
        }
      }
      // mondai5
      for (let a=0;a<bunpou.mondai5.questions.length;a++){
        let question = bunpou.mondai5.questions[a];
        for (let b=0;b<question.answerBoxs.length;b++){
          count+=4;
          let answerBox = question.answerBoxs[b];
          if(answerBox.pick){
            if(answerBox.pick==answerBox.true){
              mark+=4;
            }
            this.$store.state[this.year].bunpou.mondai5.questions[a].answerBoxs[b].pick='';
          }
        }
      }
      // mondai6
      for (let a=0;a<bunpou.mondai6.questions.length;a++){
        let question = bunpou.mondai6.questions[a];
        for (let b=0;b<question.answerBoxs.length;b++){
          count+=4;
          let answerBox = question.answerBoxs[b];
          if(answerBox.pick){
            if(answerBox.pick==answerBox.true){
              mark+=4;
            }
            this.$store.state[this.year].bunpou.mondai6.questions[a].answerBoxs[b].pick='';
          }
        }
      }
      
      score[this.year].grammer_reading = Math.round( (mark*60)/count );

      this.$store.dispatch('setScore',score);
      this.$store.dispatch('setTimeLeft',-2);
      this.parentData='score';
    },
    getTimeLeft: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.timeLeft;
    },
    setTimeLeft: function(time) {
      this.$store.dispatch('setTimeLeft',time);
    },
    start: async function() {
      this.timeleft = this.getTimeLeft();
      if(this.timeleft==-1){
        this.parentData='showresult';
      }else if(this.timeleft==-2){
        this.parentData='score';
      }else{
        await this.sleep(1000);
        if(this.timeleft>0&&this.parentData=='exam'&&this.$route.name=='bunpou'){
          this.timeleft--;
          this.setTimeLeft(this.timeleft);
          this.start();
        }else{
          this.setTimeLeft(-1);
          this.parentData='showresult';
        }
      }
    },
    sleep: function(ms) {
      return new Promise((resolve) => {
        setTimeout(resolve, ms);
      });
    }
  },
  computed: {
    title: function() {
      this.$store.dispatch('storeInit');
      return this.parentData!='score'?this.$store.state.currentTitle + " 文法 ・ 読解":'score';
    },
    year: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentYear;
    },
    type: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentType;
    },
    parent: function() {
      return this.parentData;
    },
    getTime: function() {
      let time = this.timeleft;
      let hh = Math.floor( time/60 );
      let mm = time%60;
      let format = hh+ (mm<10? ':0' : ':') +mm;
      return format;
    }
  }
}
</script>


