import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue';
import Category from '../views/CategoryView.vue';
import Loading from '../views/LoadingView.vue';
import MojiGoi from '../views/MojiGoiView.vue';
import BunPouView from '../views/BunPouView.vue';
import ChouKaiView from '../views/ChouKaiView.vue';
import ScoreView from '../views/ScoreView.vue';
import AboutView from '../views/AboutView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/category',
    name: 'category',
    component: Category
  },
  {
    path: '/loading',
    name: 'loading',
    component: Loading
  },
  {
    path: '/mojigoi',
    name: 'mojigoi',
    component: MojiGoi
  },
  {
    path: '/bunpou',
    name: 'bunpou',
    component: BunPouView
  },
  {
    path: '/choukai',
    name: 'choukai',
    component: ChouKaiView
  },
  {
    path: '/score',
    name: 'score',
    component: ScoreView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
]

const router = createRouter({
  // mode: 'history',
  history: createWebHashHistory(),
  // history: createWebHistory(),
  routes
})

export default router
